                </div>
              </div>
              <div class="btn">
                <div class="btn_01" @click="openJuniorUserDialog(item)">下级玩家({{item.LowerCount}})</div>
                <div class="btn_01" @click="openUserInfoData(item)">详情</div>
                <div class="btn_01" @click="doSaveMoney(false,item)">上分</div>
                <div class="btn_01" @click="doDrawMoney(false,item)">下分</div>
              </div>
            </div>
          </div>
        </template>
        <div v-show="tableData.length==0">没有数据</div>
      </mu-load-more>

      <mu-dialog width="360" transition="slide-bottom" fullscreen :open.sync="openDetailDialog">
        <mu-appbar color="primary" title="下级玩家列表">
          <!--        <mu-button slot="left" icon @click="UpDetailDialog">-->
