                    <div>
                      <span class="label">推荐金：</span>
                      <span class="price">{{item.Rebate}}</span>
                    </div>
                  </div>
                  <div class="btn">
                    <div class="btn_01" @click="openNextJuniorUserDialog(item)">下级玩家({{item.LowerCount}})</div>
                    <div class="btn_01" @click="openUserInfoData(item)">详情</div>
                    <div class="btn_01" @click="doSaveMoney(true,item)">上分</div>
                    <div class="btn_01" @click="doDrawMoney(true,item)">下分</div>
                  </div>
                </div>
              </div>
            </template>
            <div v-show="subData.tableData.length==0">没有数据</div>
          </mu-load-more>

        </div>
      </mu-dialog>

      <mu-dialog width="360"  transition="slide-bottom" fullscreen :open.sync="UserInfoData.showDialog">
        <mu-appbar color="primary" :title="UserInfoData.Title">
          <mu-button slot="left" flat @click="UserInfoData.showDialog = false">
            关闭
          </mu-button>
          <mu-button slot="right" flat  @click="doSaveUserInfo">
