              <mu-list-item-title>推荐码</mu-list-item-title>
              <mu-list-item-action>{{UserInfoData.UserData.Rebate}}</mu-list-item-action>
            </mu-list-item>
            <mu-list-item>
              <mu-list-item-title>可提佣金</mu-list-item-title>
              <mu-list-item-action>{{UserInfoData.UserData.ReferrerCode}}</mu-list-item-action>
            </mu-list-item>
            <mu-list-item>
              <mu-list-item-title>推荐码</mu-list-item-title>
              <mu-list-item-action>{{UserInfoData.UserData.ReferrerCode}}</mu-list-item-action>
            </mu-list-item>

            <mu-list-item>
              <mu-list-item-title>累计充值金币</mu-list-item-title>
              <mu-list-item-action>{{UserInfoData.UserData.SumSaveMoney}}</mu-list-item-action>
            </mu-list-item>
            <mu-list-item>
              <mu-list-item-title>最大充值金币</mu-list-item-title>
              <mu-list-item-action>{{UserInfoData.UserData.MaxSaveMoney}}</mu-list-item-action>
            </mu-list-item>
            <mu-list-item>
              <mu-list-item-title>累计提现金币</mu-list-item-title>
              <mu-list-item-action>{{UserInfoData.UserData.SumDrawMoney}}</mu-list-item-action>
            </mu-list-item>
            <mu-list-item>
              <mu-list-item-title>最大提现金币</mu-list-item-title>
              <mu-list-item-action>{{UserInfoData.UserData.MaxDrawMoney}}</mu-list-item-action>
            </mu-list-item>
            <mu-list-item>
              <mu-list-item-title>全部投注金额</mu-list-item-title>
              <mu-list-item-action>{{UserInfoData.UserData.SumBet}}</mu-list-item-action>
            </mu-list-item>
            <mu-list-item>
              <mu-list-item-title>全部洗码金额</mu-list-item-title>
              <mu-list-item-action>{{UserInfoData.UserData.SumXmBet}}</mu-list-item-action>
            </mu-list-item>

            <mu-list-item>
              <mu-list-item-title>全部赢得金额</mu-list-item-title>
              <mu-list-item-action>{{UserInfoData.UserData.SumWin}}</mu-list-item-action>
            </mu-list-item>
            <mu-list-item>
              <mu-list-item-title>累计上分金额</mu-list-item-title>
              <mu-list-item-action>{{UserInfoData.UserData.SumAddMoney}}</mu-list-item-action>
            </mu-list-item>
            <mu-list-item>
              <mu-list-item-title>累计下分金额</mu-list-item-title>
              <mu-list-item-action>{{UserInfoData.UserData.Sum2Rebate}}</mu-list-item-action>
            </mu-list-item>
            <mu-list-item>
              <mu-list-item-title>累计给上级的返佣</mu-list-item-title>
              <mu-list-item-action>{{UserInfoData.UserData.ReferrerCode}}</mu-list-item-action>
            </mu-list-item>
            <mu-list-item>
              <mu-list-item-title>累计返佣</mu-list-item-title>
              <mu-list-item-action>{{UserInfoData.UserData.SumRebate}}</mu-list-item-action>
            </mu-list-item>

            <mu-list-item>
              <mu-list-item-title>微信收款码Url</mu-list-item-title>
              <mu-list-item-action>{{UserInfoData.UserData.WXSKCodeUrl}}</mu-list-item-action>
            </mu-list-item>
            <mu-list-item>
              <mu-list-item-title>银行名</mu-list-item-title>
              <mu-list-item-action>{{UserInfoData.UserData.YHName}}</mu-list-item-action>
            </mu-list-item>
            <mu-list-item>
              <mu-list-item-title>卡号</mu-list-item-title>
              <mu-list-item-action>{{UserInfoData.UserData.CardNum}}</mu-list-item-action>
            </mu-list-item>

            <mu-list-item>
              <mu-list-item-title>银行用户名</mu-list-item-title>
              <mu-list-item-action>{{UserInfoData.UserData.YHUserName}}</mu-list-item-action>
            </mu-list-item>
            <mu-list-item>
              <mu-list-item-title>银行预留电话</mu-list-item-title>
              <mu-list-item-action>{{UserInfoData.UserData.YHUserTel}}</mu-list-item-action>
            </mu-list-item>
            <mu-list-item>
              <mu-list-item-title>银行预留地址</mu-list-item-title>
              <mu-list-item-action>{{UserInfoData.UserData.Addr}}</mu-list-item-action>
            </mu-list-item>
            <mu-list-item>
              <mu-list-item-title>身份证</mu-list-item-title>
              <mu-list-item-action>{{UserInfoData.UserData.Cate}}</mu-list-item-action>
            </mu-list-item>
            <mu-list-item>
              <mu-list-item-title>卡号</mu-list-item-title>
              <mu-list-item-action>{{UserInfoData.UserData.Remark}}</mu-list-item-action>
            </mu-list-item>
            <mu-list-item>
              <mu-list-item-title>银行预留信息</mu-list-item-title>
              <mu-list-item-action>{{UserInfoData.UserData.ZFBSKCodeUrl}}</mu-list-item-action>
            </mu-list-item>
            <mu-list-item>
              <mu-list-item-title>支付宝二维码Url</mu-list-item-title>
              <mu-list-item-action>{{UserInfoData.UserData.CardNum}}</mu-list-item-action>
            </mu-list-item>
            <mu-list-item>
              <mu-list-item-title>支付宝名</mu-list-item-title>
              <mu-list-item-action>{{UserInfoData.UserData.ZFBSKName}}</mu-list-item-action>
            </mu-list-item>
            <mu-list-item>
              <mu-list-item-title>用户类型</mu-list-item-title>
              <mu-list-item-action>{{UserInfoData.UserData.UserType}}</mu-list-item-action>
            </mu-list-item>
          </mu-list>

          <!--        <mu-form ref="form" :model="UserInfoData.UserData" class="mu-demo-form">-->
          <!--          <mu-form-item label="用户名" prop="UserName">-->
          <!--            <mu-text-field v-model="UserInfoData.UserData.UserName" prop="UserName"></mu-text-field>-->
          <!--          </mu-form-item>-->

          <!--          <mu-form-item label="下级会员数" prop="LowerCount">-->
          <!--            <mu-text-field v-model.number="UserInfoData.UserData.LowerCount" prop="LowerCount"></mu-text-field>-->
          <!--          </mu-form-item>-->

          <!--          <mu-form-item label="推荐码" prop="ReferrerCode">-->
          <!--            <mu-text-field  disabled v-model.number="UserInfoData.UserData.ReferrerCode" prop="ReferrerCode"></mu-text-field>-->
          <!--          </mu-form-item>-->


          <!--        </mu-form>-->
