<script>
import LoHeader from "../../components/LoHeader";
import {GetGameUserListFirstId,GetJuniorGameUserListFirstId,GetUserDetailInfo} from '../../api/GameUser';
import {SaveMoney, DrawMoney, AgreeDrawMoney} from '../../api/Finance';

export default {
  name: "GameUserList",
  components: {
    LoHeader
  },
  data () {
    return {
      num: 10,
      refreshing: false,
      loading: false,

      FilterItems:[
        {label:'用户名:',Value:'',type:"text",placeholder:"用户名",icon:"",Name:"userName"},
        {label:'用户类型:',Value:0,type:"select",placeholder:"用户名",icon:"",Name:"userType",options:this.Config.UserTypeOptions},
        {label:'开始时间:',Value:'',type:"date",placeholder:"开始时间",icon:"",Name:"beginDay"},
        {label:'结束时间:',Value:'',type:"date",placeholder:"结束时间",icon:"",Name:"endDay"},
      ],

      PageTotal:0,
      tableData: [{UserName:'测试',Gold:1,CreatedAt:'2006-01-02 15:04:05'}],
      GroupData:{},
      query: {
        beginDay: '',
        endDay: '',
        userName:'',
        userType:0,
        pageIndex: 1,
        pageSize: 100,
        FirstId:0,
      },

      subData:{
        refreshing: false,
        loading: false,
        PageTotal:0,
        tableData: [{}],
        GroupData:{},
        query: {
          pid: 0,
          state:0,
          userName:'',
          userType:0,
          pageIndex: 1,
          pageSize: 100,
          FirstId:0,
        },
        Level:0,
        arrQueryPid:[],

      },
      openDetailDialog:false,


      UserInfoData:{
        Title: '用户信息',
        showDialog:false,
        UserData:{}
      }

    }
  },
  methods: {
    refresh () {
      this.refreshing = true;
      this.query.pageIndex = 1;
      this.query.FirstId = 0;
      this.getData((v)=>{
        if (v==true){
          this.query.pageIndex = this.query.pageIndex+1
        }
        this.refreshing = false;
      });
    },
    load() {
      this.loading = true;
      this.getData((v)=>{
        if (v==true){
          this.query.pageIndex = this.query.pageIndex+1
        }
        this.loading = false;
      });
    },

    getData(fun){
      GetGameUserListFirstId(this.query).then(response => {
        const { obj, code, msg } = response;
        if (code == 200) {
          if (this.query.FirstId==0) {
            this.tableData = obj.ListData;
          } else {
            this.tableData = this.tableData.concat(obj.ListData);
          }
          this.GroupData = obj.GroupData;
          this.PageTotal = obj.PageTotal;
          this.query.FirstId = obj.GroupData.FirstId;
          fun(true);
          resolve();
        } else {
          this.$toast.error(msg);
          fun(false);
        }
      }).catch(error => {
        fun(false);
      });
    },

    getJuniorUserData(fun){
      console.log(this.subData.arrQueryPid);
      GetJuniorGameUserListFirstId(this.subData.query).then(response => {
        const { obj, code, msg } = response;
        if (code == 200) {
          if (this.subData.query.FirstId==0) {
            this.subData.tableData = obj.ListData;
          } else {
            this.subData.tableData = this.tableData.concat(obj.ListData);
          }
          this.subData.GroupData = obj.GroupData;
          this.subData.PageTotal = obj.PageTotal;
          this.subData.query.FirstId = obj.GroupData.FirstId;

          fun(true);
          resolve();
        } else {
          fun(false);
          this.$toast.error(msg);
        }
      }).catch(error => {
      });
    },
    openJuniorUserDialog(item){
      this.openDetailDialog=true;

      // this.subData.Level = 1;
      // this.subData.arrQueryPid.push(item.Id)

      this.subData.query.pageIndex = 1;
      this.subData.query.FirstId = 0;
      this.subData.query.pid = item.Id;
      this.getJuniorUserData((v)=>{
        if (v==true){
          this.subData.query.pageIndex++;
        }
      })
    },
    subRefresh(){
      this.subData.query.pageIndex = 1;
      this.subData.query.FirstId = 0;
      this.subData.refreshing = true;
      this.getJuniorUserData((v)=>{
        this.subData.refreshing = false;
        if (v==true){
          this.subData.query.pageIndex++;
        }
      })
    },
    subLoad(){
      this.subData.query.pageIndex =  this.subData.query.pageIndex + 1;
      this.subData.loading = true;
      this.getJuniorUserData((v)=>{
        this.subData.loading = false;
        if (v==true){
          this.subData.query.pageIndex++;
        }
      })
    },
    UpDetailDialog(){
      if (this.subData.Level<=0) {
        this.subData.Level = 0;
        this.openDetailDialog = false;
        this.subData.arrQueryPid.length=0;
        return
      }
      console.log(this.subData.arrQueryPid);
      this.subData.Level--;
      let pid = this.subData.arrQueryPid.pop();
      console.log(pid);
      console.log(this.subData.arrQueryPid);
      this.subData.query.pageIndex = 1;
      this.subData.query.FirstId = 0;
      this.subData.query.pid = pid;

      this.subData.query.pageIndex =  this.subData.query.pageIndex + 1;
      this.subData.loading = true;
      this.getJuniorUserData((v)=>{
        this.subData.loading = false;
        if (v==true){
          this.subData.query.pageIndex++;
        }
      })
    },
    CloseDetailDialog(){
      this.openDetailDialog = false;
      this.subData.Level = 0;
      this.subData.arrQueryPid.length = 0;
    },
    openNextJuniorUserDialog(item){
      this.openDetailDialog=true;
      this.subData.Level++;
      this.subData.arrQueryPid.push(item.Id)
      this.subData.query.pageIndex = 1;
      this.subData.query.FirstId = 0;
      this.subData.query.pid = item.Id;
      this.getJuniorUserData((v)=>{
        if (v==true){
          this.subData.query.pageIndex++;
        }
      })
    },
    doSaveMoney(isSub,item){
      let hint = '给['+item.UserName+']上分';
      this.$prompt('分数:',hint)
        .then(({ result, value }) => {
          if (result){
            let m = parseInt(value);
            let data={
              UserId:item.Id,
              Money:m
            }
            SaveMoney(data).then(response => {
              const { obj, code, msg } = response;
              if (code == 200) {
                this.getData();
                if (isSub){
                  this.subData.query.pageIndex = 1;
                  this.subData.query.FirstId = 0;
                  this.subData.refreshing = true;
                  this.getJuniorUserData((v)=>{
                    this.subData.refreshing = false;
                    if (v==true){
                      this.subData.query.pageIndex++;
                    }
                  })
                }
                this.$toast.message('上分成功.')
                resolve();
              } else {
                this.$toast.error('上分失败,原因:'+msg);
              }
            }).catch(error => {
              // this.$toast.message('上分失败,原因:'+error);
            });
          }
        });
    },
    doDrawMoney(isSub, item){
      let hint = '给['+item.UserName+']下分';
      this.$prompt('分数:',hint)
        .then(({ result, value }) => {
          if (result){
            let m = parseInt(value);
            let data={
              UserId:item.Id,
              Money:m
            }
            DrawMoney(data).then(response => {
              const { obj, code, msg } = response;
              if (code == 200) {
                this.getData();
                if (isSub){
                  this.subData.query.pageIndex = 1;
                  this.subData.query.FirstId = 0;
                  this.subData.refreshing = true;
                  this.getJuniorUserData((v)=>{
                    this.subData.refreshing = false;
                    if (v==true){
                      this.subData.query.pageIndex++;
                    }
                  })
                }

                this.$toast.message('下分成功.')
                resolve();
              } else {
                this.$toast.error('下分失败,原因:'+msg);
              }
            }).catch(error => {
              // this.$toast.error('下分失败,原因:'+error);
            });
          }
        });
    },
    openUserInfoData(item){
      this.UserInfoData.showDialog = true;

      GetUserDetailInfo({UserId:item.Id}).then(response => {
        const { obj, code, msg } = response;
        if (code == 200) {
          this.UserInfoData.UserData = obj;
          resolve();
        } else {
          this.UserInfoData.showDialog = false;
          this.$toast.error('获取数据失败,原因:'+msg);
        }
      }).catch(error => {
        // this.$toast.error('下分失败,原因:'+error);
      });
    },
    doSaveUserInfo(){

    },
    queryData(filterItems){
      this.query.pageIndex = 1;
      this.query.FirstId = 0;
      this.query.userName = '';
      this.query.userId =0;
      this.query.beginDay = '';
      this.query.endDay = '';

      for (let key in filterItems) {
        if (filterItems[key].Name === 'userName') {
          this.query.userName = filterItems[key].Value;
        } else if (filterItems[key].Name === 'userId') {
          this.query.userId = filterItems[key].Value;
        } else if (filterItems[key].Name === 'beginDay') {
          this.query.beginDay = filterItems[key].Value;
        }else if (filterItems[key].Name === 'endDay') {
          this.query.endDay = filterItems[key].Value;
        }else if (filterItems[key].Name === 'userType') {
          this.query.userType = filterItems[key].Value;
        }
      }

      this.getData((v)=>{
        if (v==true){
          this.query.pageIndex = this.query.pageIndex+1
        }
      });
    },
  },
  created() {
    this.query.pageIndex = 1;
    this.query.FirstId = 0;
    this.getData((v)=>{
      if (v==true){
        this.query.pageIndex = this.query.pageIndex+1
      }
    });
  },
}
</script>

<style scoped>
@import url('../../assets/css/lottery_num.css');

.container {
  width: 100%;
  display: flex;
  flex-direction: row;
  border-bottom: 1px solid #F2F2F2;
  justify-content: space-between;
  padding: 5px 11px;
  margin: 0 0 10px;
  background: #fff;
}
.desc {
  color: #8B8B8B;
  padding: 2px;
  text-align: left;
  font-size: 12px;
  width: 100%;
}
.title {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
}
.title_1 {
  font-weight: bold;
  color: #333;
  overflow: hidden;
  font-size: 14px;
}
.price {
  color: red;
  font-size: 12px;
  font-weight: 900;
}
.fsid {
  color: #8B8B8B;
}

.priceDiv {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
}
.xiaol {
  color: #8B8B8B;
  color: red;
}
.kc {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.btn_01 {
  border: 1px solid #8B8B8B;
  border-radius: 3px;
  text-align: center;
  margin-left: 10px;
  padding: 3px 8px;
}
.btn {
  display: flex;
  justify-content: flex-end;
}
.main {
  margin-top: 8px;
  width: 100%;
  /*margin-top: 1.4rem;*/
  margin-bottom: 1.4rem;
}
.label {

}
.time_v{
  color: red;
  font-size: 12px;
}
</style>



