import {request} from "../utils/http";

export function GetGameUserListFirstId(query) {
  return request({ url: 'api/getgameuserlistfirstid', method: 'post', data: query });
}


export function GetJuniorGameUserListFirstId(query) {
  return request({ url: 'api/getjuniorgameuserlistfirstid', method: 'post', data: query });
}

export function GetUserDetailInfo(query) {
  return request({ url: 'api/getuserdetailinfo', method: 'post', data: query });
}



